
import common from '@/libs/common.mixin'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import WorkbookClass from './artwork/class.vue'
import WorkbookPersonal from './artwork/personal.vue'

export default{
mixins: [ common ],
components: {
Tab,
TabItem,
WorkbookClass,
WorkbookPersonal,
},
data () {
return {
tab: 0,
data: [],
currentCid: 0
}
},
watch: {
tab: function (val) {
this.currentCid = 0
}
},
methods: {
detail (item) {
this.$router.push({path:'./artwork/'+item.sart_id,params:{id:item.sart_id}})
},
formatDate (time) {
time = time||''
let result = time.substring(5,10)
return result
},
handleClick (cid) {
this.currentCid = cid
}
},
computed: {
data_uri () {
return 'student_artworks?with=student_artwork_attachment'
},
style () {
let top = this.headerHeight
return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
}
}
}



