
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import Progress from 'c%/progress'
export default{
mixins: [common,globals],
components: {
Card,
Progress
},
data () {
return {
data: []
}
},
mounted () {

},
methods: {
serviceDetail (item) {
this.$router.push({path:'./service/'+item.cid})
},
getTeachPercent (item) {
return (item.attendance_times / item.arrange_times) * 100
},
itemClick (item) {
if(item.student_artwork_count > 0){
this.$emit('on-click',item.cid)
}else{
this.toast('该班级还没有作品~','text')
}
}
},
computed: {
data_uri () {
return 'classes'
},
style () {
let top = this.headerHeight + 44
return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
}
}
}



