import common from '@/libs/common.mixin'
import FileItem from 'c%/FileItem.vue'

export default{
	mixins: [ common ],
	props: {
		cid: {
			type: [ String, Number ],
			default: 0
		}
	},
	components: {
		FileItem			
	},
	data () {
		return {
			data: []
		}
	},
	methods: {
		detail (item) {
			this.$router.push({path:'./artwork/'+item.sart_id,params:{id:item.sart_id}})
		},
		formatDate (time) {
			time = time||''
			let result = time.substring(5,10)
			return result
		}
	},
	computed: {
		data_uri () {
			if(this.cid > 0){
				return `student_artworks?with=student_artwork_attachment&cid=${this.cid}`
			}
			return 'student_artworks?with=student_artwork_attachment'
		},
		style () {
			let top = this.headerHeight + 44
			return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
		}
	}
}



